import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

//outpatient
import routeList from './list';

//
var routesArray = [
    {
        path: '/index',
        name: 'Mobile Plugins',
        component: r => require.ensure([], () => r(require('../views/index.vue')), 'phone')
    },
    {
        path: '/404',
        name: '404',
        component: r => require.ensure([], () => r(require('../views/404.vue')), 'phone')
    },
    {
        path: '/500',
        name: '500',
        component: r => require.ensure([], () => r(require('../views/500.vue')), 'phone')
    }
];

routesArray = routesArray.concat(routeList);
routesArray.push({
    path: '*',
    redirect: '/index'
});

let myRouter = new Router({
    // mode: 'history',
    // scrollBehavior: () => ({ y: 0 }),
    routes: routesArray
});

export default myRouter;



