<template>
    <div class="side-bar">
        <p>Plugin List</p>
        <div id="sideBar">
            <ul>
                <li v-for="item in dataList" @click.stop="onClickSideBar(item)">{{ item.name }}</li>
            </ul>
        </div>
    </div>
</template>
<script>
    import IScroll from '../lib/picker/iscroll-probe';
    export default {
        data: function () {
            return {
                dataList: [
                    {
                        path: '/index',
                        name: 'Index'
                    },
                    {
                        path: '/list/rem',
                        name: 'Rem'
                    },
                    {
                        path: '/list/picker',
                        name: 'Picker'
                    },{
                        path: '/list/date_picker',
                        name: 'DatePicker'
                    },{
                        path: '/list/calendar',
                        name: 'Calendar'
                    }
                ]
            };
        },

        mounted(){
            //iscroll 
            setTimeout(function () {
                var wrapperSideBar = new IScroll('#sideBar', {
                    scrollX: false,
                    scrollY: true
                });
            }, 100);
        },

        methods: {
            // 
            onClickSideBar: function (item) {
                this.appConfig.appRouter.push({ path: item.path })
                this.$emit('onClickSideBar');
            }
        }
    };
</script>
<style scoped>
    .side-bar {
        font-size: 0.28rem;
        position: fixed;
        left: 0;
        top: 0;
        width: 70%;
        height: 100%;
        color: #333;
        background: #F9F9F9;
        z-index: -1;
    }
    .side-bar > p {
        font-size: 0.3rem;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 0.9rem;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        text-align: center;
        color: #ffffff;
        background: #555;
        z-index: 2;
    }
    .side-bar > div {
        overflow: hidden;
        font-size: 0.3rem;
        position: absolute;
        top: 0.9rem;
        left: 0;
        bottom: 0;
        width: 100%;
        background: #F9F9F9;
    }
    .side-bar > div > ul {
        min-height: 100%;
        padding: 0 0.2rem 0.4rem;
    }
    .side-bar > div > ul > li {
        line-height: 0.9rem;
        border-bottom: 1px solid #dddddd;
    }
    .side-bar > div > ul > li:last-child {
        /*border-bottom: none;*/
    }
</style>



