// outpatient router
export default [
    {
        path: '/list/date_picker',
        name: 'datePicker',
        // meta: {
        //     hasLoading: true,
        // },
        component: r => require.ensure([], () => r(require('../views/list/date_picker.vue')), 'phone')
    },
    {
        path: '/list/rem',
        name: 'Rem',
        component: r => require.ensure([], () => r(require('../views/list/rem.vue')), 'phone')
    },
    {
        path: '/list/picker',
        name: 'Picker',
        component: r => require.ensure([], () => r(require('../views/list/picker.vue')), 'phone')
    },
    {
        path: '/list/calendar',
        name: 'Calendar',
        component: r => require.ensure([], () => r(require('../views/list/calendar.vue')), 'phone')
    }
];




