// import Vue from 'vue'
import Axios from 'axios'
import appRouter from '../../router/index.js';
import mobileUI from '../../lib/mobileUI/mobileUI.js';

export default {
    BASE_URL: "", //

    // http
    request: function (url, params, successCallback, errorCallback) {
        var self = this;
        Axios.request({
            url: url,
            method: 'post',

            // `baseURL`  `url`  `url`  URL
            //  `baseURL`  axios  URL
            baseURL: self.BASE_URL,

            // `headers` 
            headers: {'Content-Type': 'application/x-www-form-urlencoded'},

            // `params`  URL 
            // (plain object) URLSearchParams 
            params: params,

            // `timeout` (0 )
            //  `timeout` 
            // timeout: 10000,
        }).then(function (response) {
            successCallback(response);
        }).catch(function (error) {
            errorCallback(error);
        });
    },

    //mobile UI
    mobileUI: mobileUI,

    //router 
    appRouter: appRouter,
}



