<template>
    <div id="app">
        <div class="page" :class="[animation]">
            <header class="ub app-head">
                <div class="ub-f1" @click.stop="onClickShowList()"><span class="iconfont liebiao"></span></div>
                <div class="ub-f1 title">{{ pluginName }}</div>
                <div class="ub-f1"></div>
            </header>
            <div class="content" id="content">
                <div>
                    <router-view></router-view>
                </div>
            </div>
            <div class="mask" v-show="isRight" @click.stop="onClickHideList()"></div>
        </div>

        <side-bar @onClickSideBar="onClickSideBar"></side-bar>
    </div>
</template>

<script>
    import IScroll from './lib/picker/iscroll-probe';
    import sideBar from './components/sideBar.vue';

    export default {
        name: 'app',
        data(){
            return {
                pluginName: '',
                animation: '',
                isRight: false,
                wrapperContent: null,
            }
        },

        beforeMount(){
            var self = this;

            //iscroll 
            document.addEventListener('touchmove', function (e) {
                e.preventDefault();
            }, false);

            // 
            var appRouter = self.appConfig.appRouter;
            appRouter.afterEach(to => {
                if( to.name ){
                    self.pluginName = to.name;
                }

                setTimeout(function () {
                    self.wrapperContent = new IScroll('#content', {
                        scrollX: false,
                        scrollY: true
                    });
                }, 100);
            });
        },

        components: {
            'side-bar': sideBar
        },

        methods: {
            // 
            onClickShowList(){
                this.animation = 'animated slideOutRight';
                this.isRight = true;
            },

            // 
            onClickHideList(){
                this.animation = 'animated slideInRight';
                this.isRight = false;
            },

            // sideBar
            onClickSideBar(){
                var self = this;
                setTimeout(function () {
                    self.wrapperContent.refresh();
                }, 0);
                self.onClickHideList();
            }
        }

    }
</script>

<style scoped>
    @import "./assets/css/style.css";
    @import "./assets/css/main.css";
    @import "./lib/mobileUI/mobileUI.css";
    /*@import "./assets/css/animate.css";*/
    /*@import "./assets/iconfont/publicfont/iconfont.css";*/

    .animated {
        -webkit-animation-duration: 0.4s;
        animation-duration: 0.4s;
        -webkit-animation-fill-mode: forwards;
        animation-fill-mode: forwards;
    }
    @-webkit-keyframes slideOutRight {
        from {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
        to {
            -webkit-transform: translate3d(70%, 0, 0);
            transform: translate3d(70%, 0, 0);
        }
    }
    @keyframes slideOutRight {
        from {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
        to {
            -webkit-transform: translate3d(70%, 0, 0);
            transform: translate3d(70%, 0, 0);
        }
    }
    .slideOutRight {
        -webkit-animation-name: slideOutRight;
        animation-name: slideOutRight;
    }

    @-webkit-keyframes slideInRight {
        from {
            -webkit-transform: translate3d(70%, 0, 0);
            transform: translate3d(70%, 0, 0);
        }
        to {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
    }
    @keyframes slideInRight {
        from {
            -webkit-transform: translate3d(70%, 0, 0);
            transform: translate3d(70%, 0, 0);
        }
        to {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
    }
    .slideInRight {
        -webkit-animation-name: slideInRight;
        animation-name: slideInRight;
    }
</style>


